












import axios from 'axios';

export default {
  name: 'App',
  data() {
    return {
      user: {}
    };
  },
  created() {
    axios({
      method: 'GET',
      url: 'http://localhost:5000/accounts',
    }).then((response) => {
      if(response.status === 200) {
        this.user.name = response.data;
      }
      else {
        this.user.name = 'Logged out';
      }
    });
  }
};

